import CategoryApiContext from '@/Context/CategoryApi/CategoryApiContext'
import Link from 'next/link';
import React, { useContext } from 'react'
import './Filter.scss';

function Filter({onCategoryClick}) {
    const {category} = useContext(CategoryApiContext);
    const handleClickCategory = (slug) =>{
        onCategoryClick(slug);
        
    };
  return (
    <div className='filter_area'>
        <ul className='filter_list'>
            {category.map((filtercat,index)=>(
                <li key={index}>
                    <Link href={filtercat.slug} onClick={()=> handleClickCategory(filtercat.slug)}>
                        <p>{filtercat.name}</p>
                    </Link>
                </li>
            ))}
            
        </ul>
    </div>
  )
}

export default Filter